export default function PlanDayPage() {
  const params = useParams()
  const router = useRouter()
  const { user } = useAuth()
  const [plan, setPlan] = useState<ReadingPlan | null>(null)
  const [userPlan, setUserPlan] = useState<UserReadingPlan | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [notes, setNotes] = useState('')
  const [completing, setCompleting] = useState(false)
  const [completed, setCompleted] = useState(false)

  const planId = params.id as string
  const dayNumber = parseInt(params.day as string)

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (!user || !planId || !dayNumber) {
          setError('Invalid plan or day')
          return
        }

        // Fetch plan and user plan
        const [planData, userPlans] = await Promise.all([
          readingPlanService.getReadingPlanById(planId),
          readingPlanService.getUserReadingPlans(user.id)
        ])

        if (!planData) {
          setError('Plan not found')
          return
        }

        setPlan(planData)

        const currentUserPlan = userPlans.find(up => up.plan_id === planId)
        if (!currentUserPlan) {
          setError('You have not started this plan')
          return
        }

        setUserPlan(currentUserPlan)

        // Check if this day is already completed
        const progress = await readingPlanService.getPlanProgress(user.id, planId)
        const dayProgress = progress.find(p => p.day === dayNumber)
        setCompleted(!!dayProgress?.completed)

      } catch (err) {
        setError('Error loading plan')
      } finally {
        setLoading(false)
      }
    }

    fetchData()
  }, [user, planId, dayNumber])

  const handleCompleteDay = async () => {
    if (!user || !planId || !dayNumber) return

    try {
      setCompleting(true)
      await readingPlanService.completeDay(user.id, planId, dayNumber, notes)
      setCompleted(true)
      
      // Navigate to next day or back to plan
      const nextDay = dayNumber + 1
      if (nextDay <= (plan?.duration || 0)) {
        router.push(`/plans/${planId}/day/${nextDay}`)
      } else {
        router.push(`/plans/${planId}`)
      }
    } catch (error) {
      console.error('Error completing day:', error)
    } finally {
      setCompleting(false)
    }
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
      </div>
    )
  }

  if (error || !plan) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-2xl font-bold mb-4">Error</h2>
          <p className="text-muted-foreground mb-4">{error || 'Something went wrong'}</p>
          <Button asChild>
            <Link href={`/plans/${planId}`}>
              <ArrowLeft className="mr-2 h-4 w-4" />
              Back to Plan
            </Link>
          </Button>
        </div>
      </div>
    )
  }

  // Get the reading content for this day
  const readingContent = SAMPLE_READINGS[plan.title as keyof typeof SAMPLE_READINGS]?.[dayNumber as keyof typeof SAMPLE_READINGS[keyof typeof SAMPLE_READINGS]]

  return (
    <div className="min-h-screen bg-background">
      <div className="container mx-auto px-4 py-8 max-w-4xl">
        {/* Header */}
        <div className="flex items-center justify-between mb-6">
          <Button variant="ghost" asChild>
            <Link href={`/plans/${planId}`}>
              <ArrowLeft className="mr-2 h-4 w-4" />
              Back to Plan
            </Link>
          </Button>
          
          <div className="flex items-center space-x-4">
            <Badge variant="outline">Day {dayNumber} of {plan.duration}</Badge>
            {completed && <Badge className="bg-green-100 text-green-800">Completed</Badge>}
          </div>
        </div>

        {/* Plan Info */}
        <Card className="mb-6">
          <CardHeader>
            <CardTitle className="text-xl">{plan.title}</CardTitle>
            <div className="flex flex-wrap gap-4 text-muted-foreground text-sm">
              <div className="flex items-center space-x-2">
                <Calendar className="h-4 w-4" />
                <span>{plan.duration} days</span>
              </div>
              <div className="flex items-center space-x-2">
                <Clock className="h-4 w-4" />
                <span>{plan.difficulty}</span>
              </div>
            </div>
          </CardHeader>
        </Card>

        {/* Reading Content */}
        {readingContent ? (
          <Card className="mb-6">
            <CardHeader>
              <CardTitle className="text-2xl">{readingContent.title}</CardTitle>
            </CardHeader>
            <CardContent className="space-y-6">
              {/* Scripture Passages */}
              <div>
                <h3 className="text-lg font-semibold mb-3 flex items-center">
                  <BookOpen className="mr-2 h-5 w-5" />
                  Today's Reading
                </h3>
                <div className="bg-blue-50 border-l-4 border-blue-400 p-4 rounded">
                  {readingContent.passages.map((passage, index) => (
                    <div key={index} className="text-blue-800 font-medium">
                      {passage}
                    </div>
                  ))}
                </div>
              </div>

              {/* Devotional */}
              <div>
                <h3 className="text-lg font-semibold mb-3">Devotional</h3>
                <div className="prose prose-lg max-w-none">
                  <p className="text-gray-700 leading-relaxed">
                    {readingContent.devotional}
                  </p>
                </div>
              </div>

              {/* Reflection Question */}
              <div>
                <h3 className="text-lg font-semibold mb-3">Reflection</h3>
                <div className="bg-purple-50 border-l-4 border-purple-400 p-4 rounded">
                  <p className="text-purple-800 italic">
                    {readingContent.reflection}
                  </p>
                </div>
              </div>

              {/* Notes Section */}
              <div>
                <h3 className="text-lg font-semibold mb-3">Your Notes</h3>
                <Textarea
                  placeholder="Write your thoughts, prayers, or insights from today's reading..."
                  value={notes}
                  onChange={(e) => setNotes(e.target.value)}
                  className="min-h-[120px]"
                  disabled={completed}
                />
              </div>

              {/* Action Buttons */}
              <div className="flex flex-wrap gap-3 pt-4">
                {!completed ? (
                  <Button 
                    size="lg" 
                    onClick={handleCompleteDay}
                    disabled={completing}
                  >
                    {completing ? (
                      <>
                        <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                        Completing...
                      </>
                    ) : (
                      <>
                        <CheckCircle className="mr-2 h-4 w-4" />
                        Complete Day {dayNumber}
                      </>
                    )}
                  </Button>
                ) : (
                  <Button size="lg" disabled>
                    <CheckCircle className="mr-2 h-4 w-4" />
                    Day Completed
                  </Button>
                )}
                
                {dayNumber < plan.duration && !completed && (
                  <Button variant="outline" size="lg" asChild>
                    <Link href={`/plans/${planId}/day/${dayNumber + 1}`}>
                      Next Day â†’
                    </Link>
                  </Button>
                )}
              </div>
            </CardContent>
          </Card>
        ) : (
          <Card>
            <CardContent className="text-center py-12">
              <p className="text-muted-foreground">
                Content for Day {dayNumber} is not yet available.
              </p>
            </CardContent>
          </Card>
        )}
      </div>
    </div>
  )
}
